/*
  MINIMAL RESET - add more rules as needed
*/
body {
  margin: 0;
  padding: 0;
  font-family: 'AuroRegular', system-ui, 'Open Sans', 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB',
  'Microsoft YaHei', '微软雅黑', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1rem;
  background: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

ul,
p {
  margin: 0.5rem 0;
}

fieldset {
  display: block;
}

li {
  margin: 1rem;
}

label {
  color: #999;
}

input {
  border-color: var(--color-input-border);
  color: var(--color-input-color);

  &::placeholder {
	color: black;
  }
}

sup,
sub {
  position: relative;
  font-size: 67%;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.25em;
}

a {
  text-decoration: none;
}

hr {
  border: none;
  border-top: solid 1px #ccc;
}

h4 {
  font-weight: normal;
  margin: 0.5rem 0;
}

.pre {
  white-space: pre;
}