/*
 * 1rem =
 */
html {
  font-size: 20px; /* 4K */
  @media (max-height: 2000px) {
	font-size: 19px;
  }
  @media (max-height: 1600px) {
	font-size: 18px;
  }
  @media (max-height: 1200px) {
	font-size: 17.5px;
  }
  @media (max-height: 1000px) {
	font-size: 17px;
  }
  @media (max-height: 900px) {
	font-size: 16.5px;
  }
  @media (max-height: 800px) {
	font-size: 16px;
  }
  @media (max-height: 700px) {
	font-size: 15.5px;
  }
  @media (max-height: 600px) {
	font-size: 15px;
  }
  @media (max-height: 500px) {
	font-size: 14.5px;
  }
  @media (max-height: 400px) {
	font-size: 14px;
  }
}


/*
 * Classes / Shortcuts
 */
.invisible-small {
  @media (max-width: 599px) {
	visibility: hidden;
	opacity: 0;
  }
}

.hide-small {
  @media (max-width: 599px) {
	display: none !important;
  }
}

.show-small {
  @media (min-width: 600px) {
	display: none !important;
  }
}

.show-large,
.hideIf_lt_full /* obsolete */
{
  @media (max-width: 949px) {
	display: none;
  }
}

.hide-large,
.hideIf_gt_full /* obsolete */
{
  @media (min-width: 950px) {
	display: none;
  }
}