/*
 * ANT DESIGN - RESET / FIX
 */

/* why you do this to me ant design??? */
.ant-menu-item:active, .ant-menu-submenu-title:active {
  background: none;
}

/* make checkbox check slightly bigger inside checkbox box */
.ant-checkbox-checked .ant-checkbox-inner::after {
  transform: rotate(45deg) scale(1.125) translate(-50%, -50%);
}

/* make dropdown list more beautiful */
.ant-dropdown ul, .ant-menu-root.ant-menu-vertical, .ant-menu-root.ant-menu-vertical-left, .ant-menu-root.ant-menu-vertical-right, .ant-menu-root.ant-menu-inline {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
  border-radius: 4px;
  overflow: auto;
}

/* select input dropdown (add .tld) in domain suggestions page */
.ant-select-dropdown-menu {
  max-height: calc(100vh - 17.5rem);
}

/* special case for components/ViewWord/PosWord.js, but must go into global stylesheet: */
.ant-dropdown ul.antd-word-definitions-dropdown-menu {
  margin: 0;
  padding: 0.5rem 0;
  max-width: 50vw;

  a {
	pointer-events: all;
  }

  li {
	pointer-events: none;

	line-height: 1rem !important;
	height: auto !important;
	margin: 0 1rem !important;
	padding: 0.75rem 0 !important;
	box-sizing: content-box !important;
	border-bottom: solid 1px #ccc !important;

	&:last-child {
	  border-bottom: none !important;
	}
  }
}

/* special case for components/EditWord/PosWord.js, but must go into global stylesheet: */
.ant-dropdown ul.antd-edit-posword-dropdown-menu {
  position: relative;
  left: -0.125rem;
  top: -0.5rem;

  li {
	margin: 0.75rem 0;

	&:first-child {
	  margin-top: 0.5rem;
	}

	&:last-child {
	  margin-bottom: 0.5rem;
	}
  }
}

.ant-dropdown-menu-item > a, .ant-dropdown-menu-submenu-title > a {
  color: var(--color-link);
}

.ant-input,
.ant-select-selection {
  border-color: var(--color-input-border);
}

.ant-select-selection-selected-value {
  color: var(--color-input-color);
}

.ant-input {
  font-size: 1.1rem;
  padding: 0.33rem 0.8rem;
  color: var(--color-input-color);

  &::placeholder {
	color: #999;
  }

  &.antd-height-auto {
	line-height: 1;
	height: auto;
  }
}

.ant-btn {
  font-size: 1.1rem;

  &.antd-height-auto {
	line-height: 1;
	height: auto;
  }
}

.ant-select-dropdown {
  width: auto !important;
  min-width: 10rem;

  ul {
	margin: 0;
	padding: 0.5rem;

	li {
	  margin: 0;
	  padding: 0.5rem 1rem;

	  /*
	   * Note: when using ant select, use first <Option> as a "label"
	   */
	  &:first-child {
		display: none;
	  }
	}
  }
}

.ant-message-notice-content {
  padding: 0;

  > div {
	padding: 0.75rem 1rem;
	border-radius: 0.25rem;

	&.ant-message-error {
	  border: solid 2px var(--color-bad);
	}
  }
}

.ant-checkbox {
  top: -0.19em;
}

.MenuDropdownHeaderRight {
  user-select: none;
  position: relative;
  background: var(--color-black);
  color: var(--color-light);
  border: none;
  text-align: right;
  padding: 0.25rem 0 1.25rem 0.75rem;
  white-space: nowrap;
  right: -1.5rem;
  z-index: 1000;

  li {
	user-select: none;
	padding: 0.95rem 1.5rem 0.95rem 1rem;
	margin: 0;

	h6 {
	  font-size: 1.5rem;
	  font-family: 'Quicksand', sans-serif;
	  line-height: 1rem;
	  font-weight: 400;
	  cursor: pointer;

	  &:last-child {
		border: none;
	  }
	}

	/*
	 * Colors
	 */
	color: var(--color-white);

	* {
	  color: var(--color-white);
	}

	&.subdued:not(:hover),
	&.subdued:not(:hover) * {
	  color: var(--color-light);
	}

	&:hover,
	&.selected,
	&:hover *,
	&.selected * {
	  color: var(--color-accent);
	}
  }
}

.ant-dropdown.ant-dropdown-placement-bottomRight::before {
  content: ' ';
  position: fixed;
  left: -5000px;
  width: 10000px;
  height: 4000px;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  bottom: auto;
  right: auto;
  opacity: 1;
  pointer-events: none;
  z-index: 1000;
}