/*
  GLOBAL VARIABLES:
*/
:root {
  --color-outline: #2684ff;
  --color-primary-deep: hsl(220, 37%, 52%);

  --color-primary: hsl(220, 85%, 65%); //hsl(214, 90%, 54%); //#1890ff;
  --color-link: #1890ff;

  --color-accent-deep: hsl(119, 77%, 44%);
  --color-accent: hsl(119, 57%, 57%); //hsl(120, 50%, 50%); //hsl(120, 60%, 58%); //#77c32b;
  --color-attention: orange;
  --color-good: green;
  --color-bad: #f5222d;

  --color-white: hsl(40, 40%, 99%);
  --color-offwhite: hsl(40, 40%, 96%);
  --color-light: hsl(0, 0%, 67%);
  --color-medium: #666666;
  --color-dark: #333333;
  --color-black: hsl(0, 0%, 5%);

  --color-input-border: #ccc;
  --color-input-placeholder: #ccc;
  --color-input-color: #333;
}