/*
 *
 * UI
 *
 */
button.color-bad {
  color: var(--color-bad);
  border-color: var(--color-bad);

  &:hover {
	color: var(--color-bad);
	border-color: var(--color-bad);
	opacity: 0.75;
  }

  &:focus {
	color: var(--color-bad);
	border-color: var(--color-bad);
	opacity: 0.5;
  }
}

/*
 *
 * COLORS
 *
 */
.color-primary,
.color-primary a {
  color: var(--color-link);
}

.color-accent,
.color-accent a {
  color: var(--color-accent);
}

.color-attention,
.color-attention a {
  color: var(--color-attention);
}

.color-black,
.color-black a {
  color: black;
}

.color-dark,
.color-dark a {
  color: var(--color-dark);
}

.color-medium,
.color-medium a {
  color: var(--color-medium);
}

.color-light,
.color-light a {
  color: var(--color-light);
}

.color-good,
.color-good a {
  color: var(--color-good);
}

.color-bad,
.color-bad a {
  color: var(--color-bad);
}

/*
 *
 * STYLES
 *
 */

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                   supported by Chrome, Opera and Firefox */
}

.hidden {
  visibility: hidden;
  opacity: 0;
}

.greyscale {
  filter: grayscale(100%) brightness(1.2);
}

.hmiddle {
  text-align: center;
}

.vmiddle,
.valign {
  vertical-align: middle;
}

.alignright {
  text-align: right;
}

.opacity75 {
  opacity: 0.75;
}

.opacity50 {
  opacity: 0.5;
}

.nopointer {
  pointer-events: none;
}

.noborder {
  border-bottom: none;
  border-top: none;
  border-left: none;
  border-right: none;
}

.flexrow {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.flexgrow {
  flex-grow: 1;
}

.floatright {
  float: right;
}

.columns {
  display: flex;
  width: 100%;

  .column {
	flex-grow: 1;
  }
}

.font-courier {
  font-family: "Courier New", courier, sans-serif;
}